@import url(https://fonts.googleapis.com/css2?family=Amatic+SC&family=Crete+Round&family=Fredoka+One&family=Ranchers&family=Righteous&display=swap);
body{font-family:'Amatic SC', cursive;font-family:'Crete Round', serif;font-family:'Fredoka One', cursive;font-family:'Ranchers', cursive;font-family:'Righteous', cursive}.header{color:black}.header .covid{padding:50px 0px;letter-spacing:5px;font-weight:bolder}.header .date{color:gray}.Count{margin-bottom:50px}.Count .count-box{padding:15px}.Count .count-box .count{padding:10px;box-shadow:0px 0px 10px lightgray}.Count .confirmed{color:green}.Count .confirmed .count{border-bottom:5px solid rgba(0,128,0,0.7)}.Count .deaths{color:red}.Count .deaths .count{border-bottom:5px solid rgba(255,0,0,0.7)}.Count .recovered{color:blue}.Count .recovered .count{border-bottom:5px solid rgba(0,0,255,0.7)}.count-chart{margin-bottom:50px}

